.stats-page {
  padding: 40px 40px;
}
.stats-page-stat {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
}

.stats-page-percentage-container {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.stats-page-percentage-bar {
  background: #3f51b5;
  height: 100%;
  -webkit-transition: width 500ms ease-out;
  -o-transition: width 500ms ease-out;
  transition: width 500ms ease-out;
}

.state-page-state-title {
  display: inline;
  min-width: 100px;
}

.stats-page-chart {
  max-width: 400px;
  margin-top: 40px;
}



